export const UPDATE_WINDOW_SIZE = "WINDOW/UPDATE_WINDOW_SIZE";
export const UPDATE_WINDOW_SCROLLS = "WINDOW/UPDATE_WINDOW_SCROLLS";

export const updateWindowSize = (payload) => (dispatch) => {
    dispatch({
        type: UPDATE_WINDOW_SIZE,
        payload
    });
}

export const updateWindowScrolls = (payload) => (dispatch) => {
    console.log(payload)
    dispatch({
        type: UPDATE_WINDOW_SCROLLS,
        payload
    });
}

const initialState = {
    sizes: {
        width: window.innerWidth,
        height: window.innerHeight
    },
    scrolls: {
        y: window.scrollY,
        x: window.scrollX
    }
}
export default (state = initialState, action) => {
    switch (action.type) {
        case UPDATE_WINDOW_SIZE:
            return {
                ...state,
                sizes: action.payload
            }
        case UPDATE_WINDOW_SCROLLS:
            return {
                ...state,
                scrolls: {
                    ...state.scrolls,
                    x: action.payload.scrollX,
                    y: action.payload.scrollY
                }
            }

        default:
            return state;
    }
};
