import React from "react";
import {
    BrowserRouter as Router,
    Switch,
    Route
} from "react-router-dom";
import Navbar from "@components/Navbar";
import Footer from "@components/Footer";
import { Redirect } from "react-router-dom";
import FloatingCourse from "@components/FloatingCourse";
//import TwitchScreen, { TwitchChatScreen } from '@screens/MainStack/TwitchScreen';
const HomeScreen = React.lazy(() => import('@screens/MainStack/HomeScreen'));
const AboutScreen = React.lazy(() => import('@screens/MainStack/AboutScreen'));
const SocialScreen = React.lazy(() => import('@screens/MainStack/SocialScreen'));
const ArticlesScreen = React.lazy(() => import('@screens/MainStack/ArticlesScreen'));
const StackScreen = React.lazy(() => import('@components/Trader/Stack'));
const CurrencyScreen = React.lazy(() => import('@components/Trader/Currency'));
const OpenSourceScreen = React.lazy(() => import('@screens/MainStack/OpenSourceScreen'));
const NotFoundScreen = React.lazy(() => import('@screens/MainStack/NotFoundScreen'));
const CoursesScreen = React.lazy(() => import('@screens/MainStack/CoursesScreen'));
const CalendarScreen = React.lazy(() => import('@screens/MainStack/CalendarScreen'));
const CategoryArticles = React.lazy(() => import('@components/CategoryArticles'));
const Article = React.lazy(() => import('@components/Article'));
const CourseLoader = React.lazy(() => import('@components/CourseLoader'));
//const Videos = React.lazy(() => import('@components/Videos'));
//const Video = React.lazy(() => import('@screens/MainStack/LatestVideoScreen'));
const Video = React.lazy(() => import('@screens/MainStack/VideoScreen'));
const ShortsVideoScreen = React.lazy(() => import('@screens/MainStack/ShortsVideoScreen'));
const HomeStack = (props: any) => {
    const prepareRouter = (route: string) => {
        return props?.match?.url + route;
    }
    return (
        <>
            <Navbar />
            <div className="home-stack-component-root">
                <Switch>
                    <Route path={"/"} exact>
                        <HomeScreen />
                    </Route>
                    {/* <Route path={prepareRouter("/twitch")}>
                        <TwitchScreen />
                    </Route>
                    <Route path="/twitch-chat">
                        <TwitchChatScreen />
                    </Route> */}
                    <Route path={["/social", "/sosyal-medya"]}>
                        <SocialScreen />
                    </Route>
                    <Route path={["/about", "/hakkimda"]}>
                        <AboutScreen />
                    </Route>
                    <Route path={["/courses", "/egitimlerim"]}>
                        <CoursesScreen />
                    </Route>
                    <Route path={["/kategori/:path"]}>
                        <CategoryArticles />
                    </Route>:
                    <Route path={["/yazilarim/:path"]} component={(props: any) => {
                        return <Redirect
                            to={"/kategori/" + props.match.params.path}
                        />
                    }} />
                    <Route path={["/articles", "/yazilarim"]}>
                        <ArticlesScreen />
                    </Route>
                    <Route path={["/post/:id", "/yazi/:id"]}>
                        {/* <BlogPostScreen /> */}
                        <Article />
                    </Route>
                    <Route path={["/projects", "/projelerim", "/open-source", "/acik-kaynak-projelerim"]}>
                        <OpenSourceScreen />
                    </Route>
                    <Route path={["/course/:key", "/courses/:key", "/egitimlerim/:key", "/kurs/:key"]}>
                        <CourseLoader />
                    </Route>
                    <Route path={["/video/latest"]}>
                        <Video />
                    </Route>
                    <Route path={["/calendar"]}>
                        <CalendarScreen />
                    </Route>
                    <Route path={["/video/:id", "/watch/:id", "/izle/:id"]}>
                        {/* <BlogPostScreen /> */}
                        {/* <Videos /> */}
                        <Video />
                    </Route>

                    {/* <Route path="/trade">
                            <Route path={["/trade/:key"]}>
                                <TraderScreen />
                            </Route>
                        </Route> */}
                    <Route path="/stack">
                        <Route path={["/stack/:stack/:key"]}>
                            <StackScreen />
                        </Route>
                    </Route>
                    <Route path="/currency">
                        <Route path={["/currency/:stack/:key"]}>
                            <CurrencyScreen />
                        </Route>
                    </Route>
                    <Route path="/shorts/:id">
                        <ShortsVideoScreen />
                    </Route>
                    <Route>
                        <NotFoundScreen />
                    </Route>
                </Switch>
            </div>
            <Footer />
            <FloatingCourse />
        </>
    )
}


export default HomeStack;