const initialState = {
    fetchingAboutMe: false,
    errorFetchingAboutMe: false,
    aboutMe: undefined,
    fetchingMainArticles: false,
    errorFetchingMainArticles: false,
    mainArticles: [],
    fetchingPopularArticles: false,
    errorFetchingPopularArticles: false,
    popularArticles: [],
    fetchingArticle: false,
    errorFetchingArticle: false,
    articles: {},
    fetchingCategoryArticles: false,
    errorFetchingCategoryArticles: false,
    categoryArticles: {
    },
    fetchingCategoryDetails: false,
    errorFetchingCategoryDetails: false,
    categoryDetails: {},
    fetchingCategories: false,
    errorFetchingCategories: false,
    categories: [],
    fetchingPopularShorts: false,
    errorFetchingPopularShorts: false,
    popularShorts: [],
    fetchingShort: false,
    errorFetchingShort: false,
    shorts: {},
    fetchingVideo: false,
    errorFetchingVideo: false,
    videos: {},
    fetchingCourses: false,
    errorFetchingCourses: false,
    courses: [],
}

export default (state = initialState, action) => {
    switch (action.type) {
        case GET_MAIN_ARTICLES:
            return {
                ...state,
                fetchingMainArticles: true,
                errorFetchingMainArticles: false,
            }
        case GET_MAIN_ARTICLES_SUCCESS:
            return {
                ...state,
                fetchingMainArticles: false,
                errorFetchingMainArticles: false,
                mainArticles: _.uniqBy([...state?.mainArticles, ...action.payload?.data], "id")
            }
        case GET_MAIN_ARTICLES_FAILED:
            return {
                ...state,
                fetchingMainArticles: false,
                errorFetchingMainArticles: true,
                mainArticles: []
            }
        case GET_ARTICLE:
            return {
                ...state,
                fetchingArticle: true,
                errorFetchingArticle: false
            }
        case GET_ARTICLE_SUCCESS:
            return {
                ...state,
                fetchingArticle: false,
                errorFetchingArticle: false,
                articles: {
                    ...state.articles,
                    [action.payload?.path]: action.payload?.data
                }
            }
        case GET_ARTICLE_FAILED:
            return {
                ...state,
                fetchingArticle: false,
                errorFetchingArticle: true,
                articles: {
                    ...state.articles,
                    [action.payload?.path]: null
                }
            }
        case GET_POPULAR_ARTICLES:
            return {
                ...state,
                fetchingPopularArticles: true,
                errorFetchingPopularArticles: false,
            }
        case GET_POPULAR_ARTICLES_SUCCESS:
            return {
                ...state,
                fetchingPopularArticles: false,
                errorFetchingPopularArticles: false,
                popularArticles: _.uniqBy([...state?.popularArticles, ...action.payload?.data], "id")
            }
        case GET_POPULAR_ARTICLES_FAILED:
            return {
                ...state,
                fetchingPopularArticles: false,
                errorFetchingPopularArticles: true,
                popularArticles: []
            }
        case GET_CATEGORY_ARTICLES:
            return {
                ...state,
                fetchingCategoryArticles: true,
                errorFetchingCategoryArticles: false,
            }
        case GET_CATEGORY_ARTICLES_SUCCESS:
            return {
                ...state,
                fetchingCategoryArticles: false,
                errorFetchingCategoryArticles: false,
                categoryArticles: {
                    ...state.categoryArticles,
                    [action.payload?.path]: action.payload?.data
                }
            }
        case GET_CATEGORY_ARTICLES_FAILED:
            return {
                ...state,
                fetchingCategoryArticles: false,
                errorFetchingCategoryArticles: true,
                categoryArticles: {
                    ...state.categoryArticles,
                    [action.payload?.path]: []
                }
            }
        case GET_CATEGORY_DETAILS:
            return {
                ...state,
                fetchingCategoryDetails: true,
                errorFetchingCategoryDetails: false,
            }
        case GET_CATEGORY_DETAILS_SUCCESS:
            return {
                ...state,
                fetchingCategoryDetails: false,
                errorFetchingCategoryDetails: false,
                categoryDetails: {
                    ...state.categoryDetails,
                    [action.payload?.path]: action.payload?.data
                }
            }
        case GET_CATEGORY_DETAILS_FAILED:
            return {
                ...state,
                fetchingCategoryDetails: false,
                errorFetchingCategoryDetails: true,
                categoryDetails: {
                    ...state.categoryDetails,
                    [action.payload?.path]: null
                }
            }
        case GET_CATEGORIES:
            return {
                ...state,
                fetchingCategories: true,
                errorFetchingCategories: false,
            }
        case GET_CATEGORIES_SUCCESS:
            return {
                ...state,
                fetchingCategories: false,
                errorFetchingCategories: false,
                categories: _.uniqBy([...state?.categories, ...action.payload?.data], "id")
            }
        case GET_CATEGORIES_FAILED:
            return {
                ...state,
                fetchingCategories: false,
                errorFetchingCategories: true,
                categories: []
            }
        case GET_POPULAR_SHORTS:
            return {
                ...state,
                fetchingPopularShorts: true,
                errorFetchingPopularShorts: false,
            }
        case GET_POPULAR_SHORTS_SUCCESS:
            return {
                ...state,
                fetchingPopularShorts: false,
                errorFetchingPopularShorts: false,
                popularShorts: _.uniqBy([...state?.popularShorts, ...action.payload?.data], "id")
            }
        case GET_POPULAR_SHORTS_FAILED:
            return {
                ...state,
                fetchingPopularShorts: false,
                errorFetchingPopularShorts: true,
                popularShorts: []
            }
        case GET_SHORT:
            return {
                ...state,
                fetchingShort: true,
                errorFetchingShort: false
            }
        case GET_SHORT_SUCCESS:
            return {
                ...state,
                fetchingShort: false,
                errorFetchingShort: false,
                shorts: {
                    ...state.shorts,
                    [action.payload?.path]: action.payload?.data
                }
            }
        case GET_SHORT_FAILED:
            return {
                ...state,
                fetchingShort: false,
                errorFetchingShort: true,
                shorts: {
                    ...state.shorts,
                    [action.payload?.path]: null
                }
            }
        case GET_VIDEO:
            return {
                ...state,
                fetchingVideo: true,
                errorFetchingVideo: false
            }
        case GET_VIDEO_SUCCESS:
            return {
                ...state,
                fetchingVideo: false,
                errorFetchingVideo: false,
                videos: {
                    ...state.videos,
                    [action.payload?.path]: action.payload?.data
                }
            }
        case GET_VIDEO_FAILED:
            return {
                ...state,
                fetchingVideo: false,
                errorFetchingVideo: true,
                videos: {
                    ...state.videos,
                    [action.payload?.path]: null
                }
            }
        case GET_ABOUT_ME:
            return {
                ...state,
                fetchingAboutMe: true,
                errorFetchingAboutMe: false
            }
        case GET_ABOUT_ME_SUCCESS:
            return {
                ...state,
                fetchingAboutMe: false,
                errorFetchingAboutMe: false,
                aboutMe: action.payload?.data
            }
        case GET_ABOUT_ME_FAILED:
            return {
                ...state,
                fetchingAboutMe: false,
                errorFetchingAboutMe: true,
                aboutMe: null
            }
        case GET_COURSES:
            return {
                ...state,
                fetchingCourses: true,
                errorFetchingCourses: false
            }
        case GET_COURSES_SUCCESS:
            return {
                ...state,
                fetchingCourses: false,
                errorFetchingCourses: false,
                courses: action.payload?.data.map((course) => {
                    const date = new Date();
                    const currentYearAddingParam = (date.getFullYear() - 2022) * 12;
                    const currentNumber: number = date.getMonth() + 1 + currentYearAddingParam;
                    const couponCode = course?.attributes?.couponCode?.toUpperCase() + currentNumber;
                    return {
                        ...course,
                        ...course?.attributes,
                        couponCode: couponCode,
                    }
                }
                )
            }
        case GET_COURSES_FAILED:
            return {
                ...state,
                fetchingCourses: false,
                errorFetchingCourses: true,
                courses: null
            }
        default:
            return state;
    }
};
