import React, { Component, useEffect } from 'react';
import { connect } from 'react-redux';
import styles from './index.module.scss';

import Course from '@components/Course';
import classNames from 'classnames';
import { BiGift, BiStar } from 'react-icons/bi';
import { SiStardock, SiUdemy } from 'react-icons/si';
import { FaSurprise } from 'react-icons/fa';
import { BsGift, BsGiftFill } from 'react-icons/bs';

const mapStateToProps = (state: any) => ({
    cms: state.cms
})

const mapActionsToProps = {
}

const FloatingCourse: React.FC<any> = (props) => {
    const [showBackground, setShowBackground] = React.useState(false);

    if (!props?.cms?.courses?.[1]) return null;
    return (<><div className={styles.course} onMouseOver={() => {
        setShowBackground(true);
    }} onMouseLeave={() => {
        if (showBackground) {
            setShowBackground(false);
        }
    }}>
        <div className={styles.star}>
            <BsGiftFill size={45} />
        </div>
        <Course
            item={props?.cms?.courses?.[1]}
            className={classNames(styles.floatingCourse)}
            border={showBackground ? "5px solid white" : "5px solid #FFDF00"}
        />
    </div>
        {showBackground && <div className={styles.backgroundBlur}></div>}
    </>
    )
}

export default connect(mapStateToProps, mapActionsToProps)(FloatingCourse);