import React, { useEffect } from "react";
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import {
    BrowserRouter as Router,
    Switch,
    Route
} from "react-router-dom";
import Navbar from "@components/Navbar";
import Footer from "@components/Footer";
import ScrollToTop from '@components/ScrollToTop';
import PageLoader from '@components/Loader/index';
import HomeStack from "./HomeStack";
import FloatingCourse from "@components/FloatingCourse";


const MainNavigation = (props: object) => {
    useEffect(() => {
        window.addEventListener("load", () => {
            const winObj: any = window;
            if (winObj?.Superpeer) {
                if (!(window.location.href.includes('/dashboard') || window.location.href.includes('/admin'))) {

                    new winObj.Superpeer.Widget({ embed: { type: "popup" }, launcher: { type: "profile-avatar", options: {} }, config: { username: "cangokceaslan", serviceSlug: "" } })
                }
            }
        })
    }, [])

    return (
        <div className="main">
            <ToastContainer />
            <React.Suspense fallback={<PageLoader loaded />}>
                <Router>
                    <ScrollToTop />

                    <Switch>
                        <Route path={"/"}>
                            <HomeStack />
                        </Route>
                        {/* <Route>
                            <NotFoundScreen />
                        </Route> */}
                    </Switch>
                </Router>
            </React.Suspense>
        </div>

    )
}


export default MainNavigation;